.form-parametrs {
  margin: 0 auto;
  padding: 0;
  width: auto;
}

.form-parametrs__container {
  margin: 0;
  padding: 0;
}

.form-parametrs__box {
  margin: 0 auto;
  width: 280px;

  @media (min-width: $tablet-width) {
    width: 628px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 72px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    flex-direction: row;
    gap: 0;
  }
}

.form-parametrs__box-info {
  margin: 0;
  padding: 0 0 21px 0;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 12px;

  @media (min-width: $tablet-width) {
    gap: 13px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    gap: 14px;
  }
}

.form-parametrs__box-program {
  margin: 0 -20px;
  padding: 22px 20px 15px 18px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 9px;
  border: 1px solid #d9d9d9;
  width: 280px;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 23px 0 27px 27px;
    gap: 8px;
    border: 1px solid #e7e7e7;
    width: auto;
  }

  @media (min-width: $desktop-width) {
    padding: 30px 27px 20px;
    gap: 0;
    width: 516px;
  }
}

.form-parametrs__contact-title-wrapper {
  display: flex;
  gap: 20px;

  @media (min-width: $tablet-width) {
    gap: 40px;
  }
}

.form-parametrs__contact-title-wrapper::before {
  content: " ";
  width: calc(50% - 160px);
  height: 42px;
  border-bottom: 2px solid #68b738;

  @media (min-width: $tablet-width) {
    width: calc(50% - 356px);
    height: 84px;
  }

  @media (min-width: $desktop-width) {
    width: calc(50% - 650px);
    height: 91px;
  }
}

.form-parametrs__contact-title-wrapper::after {
  content: " ";
  width: 10px;
  height: 42px;
  border-bottom: 2px solid #68b738;
  flex-grow: 1;

  @media (min-width: $tablet-width) {
    height: 84px;
  }

  @media (min-width: $desktop-width) {
    width: calc(50% - 650px);
    height: 91px;
  }
}

.form-parametrs__contact-title {
  margin: 0;
  padding: 26px 0 34px 0;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  width: 187px;

  @media (min-width: $tablet-width) {
    padding: 62px 0 69px;
    font-size: 36px;
    line-height: 36px;
    width: auto;
  }

  @media (min-width: $desktop-width) {
    padding: 70px 0 74px;
    font-size: 36px;
    line-height: 36px;
  }
}

.form-parametrs__contact-address {
  margin: 0 auto;
  padding: 0;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  width: 280px;

  @media (min-width: $tablet-width) {
    width: 628px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
  }
}

.form-parametrs__comment {
  margin: 0;
  padding: 21px 0 38px;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;

  @media (min-width: $tablet-width) {
    padding: 63px 0 98px;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    padding: 72px 0 78px;
    font-size: 36px;
    line-height: 36px;
  }
}

.form-parametrs__comment-wrapper {
  display: flex;
  gap: 20px;

  @media (min-width: $tablet-width) {
    gap: 40px;
  }
}

.form-parametrs__comment-wrapper::before {
  content: " ";
  width: calc(50% - 160px);
  height: 36px;
  border-bottom: 2px solid #68b738;

  @media (min-width: $tablet-width) {
    width: calc(50% - 356px);
    height: 84px;
  }

  @media (min-width: $tablet-width) {
    width: calc(50% - 356px);
    height: 84px;
  }

  @media (min-width: $desktop-width) {
    width: calc(50% - 650px);
    height: 94px;
  }
}

.form-parametrs__comment-wrapper::after {
  content: " ";
  width: 10px;
  height: 36px;
  border-bottom: 2px solid #68b738;
  flex-grow: 1;

  @media (min-width: $tablet-width) {
    height: 84px;
  }

  @media (min-width: $desktop-width) {
    height: 94px;
  }
}

.form-parametrs__comment-info-wrapper {
  margin: 0 auto;
  width: 280px;

  @media (min-width: $tablet-width) {
    width: 628px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
  }
}

.form-parametrs__comment-info {
  margin: 0;
  padding: 14px 0 18px 15px;
  width: 263px;
  min-height: 116px;
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;

  @media (min-width: $tablet-width) {
    padding: 0 0 0 18px;
    width: 608px;
    height: 162px;
    font-size: 20px;
    line-height: 30px;
    text-align: start;
  }

  @media (min-width: $desktop-width) {
    padding: 9px 0 0 20px;
    width: 1200px;
    min-height: 175px;
    font-size: 20px;
    line-height: 30px;
  }
}

.form-parametrs__addition-wrapper {
  display: flex;
  gap: 20px;

  @media (min-width: $tablet-width) {
    gap: 40px;
  }
}

.form-parametrs__addition-wrapper::before {
  content: " ";
  width: calc(50% - 160px);
  height: 34px;
  border-bottom: 2px solid #68b738;

  @media (min-width: $tablet-width) {
    width: calc(50% - 356px);
    height: 83px;
  }

  @media (min-width: $desktop-width) {
    width: calc(50% - 650px);
    height: 79px;
  }
}

.form-parametrs__addition-wrapper::after {
  content: " ";
  width: 10px;
  height: 34px;
  border-bottom: 2px solid #68b738;
  flex-grow: 1;

  @media (min-width: $tablet-width) {
    height: 83px;
  }

  @media (min-width: $desktop-width) {
    width: calc(50% - 650px);
    height: 79px;
  }
}

.form-parametrs__addition {
  margin: 0 auto;
  padding: 19px 0 36px;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;

  @media (min-width: $tablet-width) {
    padding: 60px 0 57px;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    padding: 55px 0 57px;
  }
}

.form-parametrs__addition-info {
  margin: 0 auto;
  padding: 0 0 34px 0;
  width: 100%;

  @media (min-width: $tablet-width) {
    padding: 0 0 54px 0;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 62px 0;
  }
}

.form-parametrs__subscribe-box {
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    margin: 0 auto;
    padding: 0 0 60px 0;
    display: flex;
    gap: 64px;
    align-items: center;
    width: 628px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 81px 0;
    width: 1220px;
    gap: 80px;
  }
}

.form-parametrs__subscribe {
  margin: 0 auto;
  padding: 10px 0;
  width: 280px;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 14px 0;
    width: 282px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
  }
}

.form-parametrs__attantion {
  margin: 0;
  padding: 21px 0 30px;
  color: #000000;
  text-align: center;
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    display: inline-flex;
    font-size: 16px;
  }
}
